import React from "react"
import { Link } from "gatsby"



const CategoryListWrapper = (props) => <div className={'flex flex-warp w-[768px] mt-36 mx-auto'}>{props.children}</div>
const CategoryItem = ({ active, ...props }) => <Link className={`${active ? 'font-[800]' : 'font-[400]'} ${active ? 'text-black' : 'text-stone-500'} mr-4 py-1 px-0 text-[1.125rem] cursor-pointer last-of-type:mr-0`}>{props.children}</Link>


export interface ICategoryListProps {
    selectedCategory: string,
    categoryList: {
        [key: string]: number,
    }
    //children?: React.ReactNode,
}

const CategoryList = (
    {
        selectedCategory,
        categoryList,
    }: ICategoryListProps
) => {

    return (
        // <div className={'flex flex-warp w-[768px] mt-[100px] mx-auto mb-0'}>
        <CategoryListWrapper>
            {Object.entries(categoryList).map(([name, count]) => (
                <CategoryItem key={name}
                              to={`/?category=${name}`}
                              active={name === selectedCategory}
                >
                    #{name}({count})
                </CategoryItem>
            ))}
        </CategoryListWrapper>
    )
}


CategoryList.defaultProps = {}

export default CategoryList