import React from "react"
import ProfileImage from "components/main/ProfileImage"
import { md } from "../../styles/helpers/TailwindUtil"
import { IGatsbyImageData } from "gatsby-plugin-image"


const Background = (props) => <div
    className={"w-full bg-gradient-to-br from-[#29323c] to-[#485563] text-white"}>{props.children}</div>
const Wrapper = (props) => <div
    className={`flex flex-col justify-center items-start my-0 mx-auto w-full h-[300px] py-0 px-3 ${md("w-[768px] h-[400px]")} `}>{props.children}</div>
const Title = (props) => <div className={`text-[1.5rem] font-bold mt-2 ${md("text-[2.2rem]")}`}>{props.children}</div>
const SubTitle = (props) => <div className={`text-[1rem] font-normal ${md("text-[1.25rem]")}`}>{props.children}</div>


export interface IIntroductionProps {
    profileImage: IGatsbyImageData,
    //children?: React.ReactNode,
}


const Introduction = (
    {
        profileImage
    }: IIntroductionProps
) => {

    return (
        <Background>
            <Wrapper>
                <ProfileImage profileImage={profileImage} />
                <div>
                    <Title> </Title>
                    <SubTitle>Hello, I'm Jeon Min Bae's blog. Kim Jaehyun will talk about 1,538 ways to
                        bully.</SubTitle>
                </div>
            </Wrapper>
        </Background>
    )
}


Introduction.defaultProps = {}

export default Introduction